* {
    margin: 0;
    padding: 0;
}

img {
    display: block;
    max-width: 100%;
}

ul {
    list-style: none;
}

body {
    background: $bg-light;
    color: $gray;
    -webkit-font-smoothing: antialiased;
}

.title {
    font-family: $title;
    font-size: 6rem;
    line-height: 1;
    font-weight: 400;
    margin-bottom: 1rem;
}

.animals p {
    font-family: $paragraph;
}